.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.byline,
.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
	font-size: 20px;
	color: $color-brown;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}
.hero-pusher {
	width: 100%;
	float: left;

	@media screen and (min-width: $tabwidth) {
		display: none;
	}
	@media screen and (min-width: $deskwidth) {
		min-height: 550px;
		display: block;
	}
	@media screen and (min-width: $largewidth) {
		min-height: 620px;
	}
	@media screen and (min-width: $xlargewidth) {
		min-height: 680px;
	}

}
section.hero {
	width: 100%;
	float: left;
	position: relative;
	line-height: 0;
	// z-index: 1;

	@media screen and (min-width: $tabwidth) {
		
	}
	@media screen and (min-width: $deskwidth) {
		position: absolute;
		top: -0px;
		left: 0;
	}
	@media screen and (min-width: $xlargewidth) {
		max-height: 900px;
	}


	img {
		width: 100%;
	}

	.hero-text {
		line-height: 1.2;
		color: #fff;
		width: 90%;
		text-align: center;
		font-size: 20px;
		position: absolute;
		top: 20%;
		left: 5%;

		@media screen and (min-width: $tabwidth) {
			width: 80%;
			font-size: 30px;
			left: 10%;
		}
		@media screen and (min-width: $deskwidth) {
			width: 50%;
			top: 40%;
			left: 25%;
			font-size: 36px;
		}
	}
}
section.page-contents {
	width: 80%;
	margin: 0 auto;
	text-align: left;
	padding-bottom: 100px;
}
.entry-content.menus {
	text-align: center;
}
.smenu-item {
	width: 90%;
	display: inline-block;
	margin: 20px auto;
	position: relative;

	@media screen and (min-width: $tabwidth) {
		width: 40%;
		margin: 2%;
	}
	@media screen and (min-width: $deskwidth) {
		width: 1/3*86%;
	}

	.menu-image {
		width: 100%;
		// border-left: 1px solid #eee;
		// border-bottom: 1px solid #eee;
		// border-right: 1px solid #eee;
		float: left;
		line-height: 0;
		img {
			width: 100%;
		}
	}
}
.menu-top-button {
	width: 100%;
	float: left;
	padding: 20px 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: $color-brown;
	.small-button {
		width: 80%;
		padding: 2%;
		color: #fff;
		border: 1px solid #fff;
		text-align: center;
		a {
			text-decoration: none;
			color: #fff;
			display: block;
			@include transition(all ease .3s);
			&:hover {
				background-color: #fff;
				color: $color-brown;
			}
		}
	}
}
.menu-top-button-page {
	width: auto;
	padding: 20px 0;
}
.contact-left {
	width: 100%;
	margin: 0 0 30px 0;
	float: left;
	@media screen and (min-width: $tabwidth) {
		width: 45%;
	}
}
.contact-right {
	width: 100%;
	margin: 0 0 30px 0;
	float: left;
	@media screen and (min-width: $tabwidth) {
		width: 45%;
		float: right;
	}
}